$blue: #2196F3;
$darkBlue: #1a6aa9;
$white : #FFF;
$darkGrey: #b9b9b9;
$lightGrey: #e7e7e7;
$smallText: 12px;
$mediumText: 14px;

* {
  font-size: $mediumText;
}

.flex {
  display: flex;
  justify-content: center;
  align-items: center;
}



.button.start-test:hover {
  background-color: $darkBlue;
}

.button.start-test {
  background-color: $blue;
  color: $white;
  margin-top: 15px;
  font-weight: bold;
}

.button {
  font-size: 16px;
  cursor: pointer;
  padding: 10px 15px;
  border-radius: 5px;
}

.main-view {
  padding: 10px;
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.side-menu {
  width: 25%;
  min-width: 430px;
}

.middle-view {
  display: flex;
  align-items: center;
  justify-content: center;
}

.title-bar {
  background-color: $blue;
  color: $white;
  text-align: center;
  padding: 5px 0;
  margin-bottom: 10px;
}

.tabs {
  display: flex;
  font-size: $smallText;
}

.tabs .tab {
  cursor: pointer;
  padding: 10px;
  border: 1px solid $darkGrey;
  margin-right: 2px;
  background-color: $lightGrey;
  z-index: 0;
}

.tabs .tab.active {
  font-size: 16px;
  background-color: $white;
  font-weight: bold;
  border-bottom: 1px solid $white;
  z-index: 3;
}

.side-menu .content {
  border: 1px solid $darkGrey;
  padding: 15px;
  margin-top: -2px;
  background-color: $white;
  z-index: 2;
  display: block;
  position: relative;
}

.side-menu .content .option {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}


.side-menu .content .option .input-text.small {
  margin-right: 10px;
  max-width: 100px;
}

.side-menu .content .option .input-text {
  font-size: $mediumText;   
  padding: 5px;
  width: 65%;
}

.side-menu .content .option .current-range {
  display: flex;
  margin-top: 10px;
}

.side-menu .content .option .grey-box {
  cursor: pointer;
  background-color: $lightGrey;
  padding: 5px 10px;
  border-radius: 6px;
  margin-right: 5px;
  text-align: center;
  font-size: $smallText;
}


.side-menu .content .option .grey-box.active {
  background-color: $blue;
  color: $white;
}

.side-menu .content .title {
  background-color: $lightGrey;
  color: grey;
  font-weight: bold;
  padding: 5px;
  margin: 20px 0 10px 0;
}

.side-menu .content .section {
  margin-left: 15px;
}

.green-button:hover {
  background-color: #00cc22;
}
.green-button {
  cursor: pointer;
  background-color: #00AA22;
  padding: 10px;
  border-radius: 15px;
}
